.soltivka-class {
  color: red;
}
.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-height: 200px;
  min-height: 180px;
  width: 150px;
  border-radius: 30px;
  padding: 20px;
  background-color: #3D62CF;
  margin: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}
.img-container{
  display:flex;
  align-items: center;
  width:100%;
  height:80%;
}

.card-image{
  width: 100%;
  max-height: 150px;
}
.card-button{
  cursor: pointer;
  display:block;
  margin: 10px;
  background-color: #3D62CF;
  color: white;
  border: none;
  padding: 5px 10px 0 10px;
  text-align: center;
  border-left: solid;
  border-right: solid;
  border-radius: 30px;
  border-color: #009afa;
  border-width: 1px;
  min-width: 100px;
  max-width: 80%;
}
.card-button:hover{
  background-image: linear-gradient(to top, #009afa 0%,#3D62CF 50%, #3D62CF 100%);
  background-size: 200% auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0,.5);
  transition: .3s;
}