
.header {
    position: sticky;
    top: 0;
    left: 0;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(0, 154, 250, .38);
    background-image: url(./resources/header.png);
    background-size: cover;
    background-position: left;
    padding-left: 20px;
    height: 50px;
    opacity: 0.9;
    z-index: 10;

    box-shadow: 0 20px 40px rgba(0, 154, 250, .5);
}

#sitename {
    color: white;
    font-size: 30px;
    margin: 10px;
}

.header-buttons {
    display: flex;
    flex-wrap: wrap;
    width: 60%;
    justify-content: space-between;
}

.header_button {
    cursor: pointer;
    display: block;
    margin: 10px;
    color: white;
    background-color: #3D62CF;
    border: none;
    padding: 5px;
    text-align: center;
    border-left: solid;
    border-right: solid;
    border-radius: 30px;
    border-color: #009afa;
    border-width: 1px;
    min-width: 100px;
    max-width: 80%;
    outline: none;
    transition: .3s;
}

.header_button:hover {
    background-image: linear-gradient(to top, #3D62CF 0%, #3D62CF 50%, #009afa 100%);
    border-color: #3D62CF;
    background-size: 200% auto;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
    transition: .3s;
}