.gameContainer {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #009afa;
}

.questionContainer {
    display: flex;
    width: 100%;
    max-width: 1100px;
    margin: 20px;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #009afa;
}

.statsContainer {
    display: flex;
    flex-direction: column;
    margin: 20px;
    width: 40%;
}

.buttons_container {
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
}

.question {
    color: white;
    width: 100%;
    text-align: center;
}
.difficultyContainer{
    margin-top:200px;
    display: flex;
    flex-direction: column;
    max-width: 1100px;
}
.red{
    color: darkred;
}
.green{
    color:forestgreen;
}
.orange{
    color:orange;
}