button {
    padding: 0;
    border: none;
    font: inherit;
    color: white;
    background-color: transparent;
}

.button {
    cursor: pointer;
    display: block;
    margin: 10px;
    background-color: #009afa;
    border: none;
    padding: 5px 10px 0 10px;
    text-align: center;
    border-left: solid;
    border-right: solid;
    border-radius: 30px;
    border-color: #3D62CF;
    border-width: 1px;
    min-width: 100px;
    max-width: 80%;
    outline: none;
    transition: .3s;
}

.button:hover {
    background-image: linear-gradient(to top, #009afa 0%, #009afa 50%, #3D62CF 100%);
    background-size: 200% auto;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
    transition: .3s;
    border-color: #009afa;
}

